import React,{Component} from 'react'
import { hashHistory, Link } from 'react-router'
import { Row, Col, Button, message, Select, Form, Cascader } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const categoryOps = [{
    value: '0',
    label: ''
},{
    value: '1',
    label: ''
},{
    value: '2',
    label: '',
    children: [{
        value: '2-1',
        label: ''
    }, {
        value: '2-2',
        label: ''
    }]
},{
    value: '3',
    label: ''
}];
class FilterForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            category: '0',
            type: [],
            filterData: {
                debts: [],
                stock: [],
                assets: [],
                typeList:[],
                industryList: [],
                province_list: {},
                financingNumList: []
            }
        };
        ['handleSubmit', 'changeCategory'].forEach(m=>this[m] = this[m].bind(this));
    }

    componentWillMount() {
        let that = this;
        ajax({
            url: "/fund/list/getFilterInfo",
            callback(data) {
                that.setState({
                    filterData: Object.assign(data, {typeList: []})
                });
                that.changeCategory(that.state.category);
                that.handleSubmit(1);
            }
        })
    }

    changeCategory(val) {
        let type;
        switch(val[0].toString()) {
            case '0':
                type = [];
                break;
            case '1':
                type = this.state.filterData.debts;
                break;
            case '2':
                type = this.state.filterData.stock;
                break;
            case '3':
                type = this.state.filterData.assets;
                break;
        }
        this.state.filterData.typeList = type;
        this.setState({
            filterData: this.state.filterData
        });
        this.props.form.setFieldsValue({
            type: [],
            industry: [],
            region: [],
            financingNum: '0'
        });
    }

    handleSubmit(page) {
        this.props.form.validateFieldsAndScroll((err, values) => {
            this.props.getFilterResult(Object.assign(values, {page}));
        })
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        var regionOps = [];
        for(var key in this.state.filterData.province_list) {
            regionOps.push({
                key,
                value: this.state.filterData.province_list[key]
            });
        }
        return (
            <Form layout="inline">
                <FormItem>
                    {getFieldDecorator('category', {
                        initialValue: [this.state.category]
                    })(
                        <Cascader allowClear={false} onChange={this.changeCategory} changeOnSelect placeholder="" options={categoryOps} />
                    )}
                </FormItem>
                <FormItem>
                    {getFieldDecorator('type', {
                        initialValue: []
                    })(
                        <Select
                            className="multiSelect"
                            placeholder=""
                            mode="multiple"
                        >
                            {this.state.filterData.typeList.map(item =>
                                <Option key={item.key} value={item.key.toString()}>{item.value}</Option>
                            )}
                        </Select>
                    )}
                </FormItem>
                <FormItem>
                    {getFieldDecorator('industry', {
                        initialValue: []
                    })(
                        <Select
                            className="multiSelect"
                            placeholder=""
                            mode="multiple"
                        >
                            {this.state.filterData.industryList.map(item =>
                                <Option key={item.key} value={item.key.toString()}>{item.value}</Option>
                            )}
                        </Select>
                    )}
                </FormItem>
                <FormItem>
                    {getFieldDecorator('region', {
                        initialValue: []
                    })(
                        <Select
                            className="multiSelect"
                            placeholder=""
                            mode="multiple"
                        >
                            {regionOps.map(item =>
                                <Option key={item.key} value={item.key.toString()}>{item.value}</Option>
                            )}
                        </Select>
                    )}
                </FormItem>
                <FormItem>
                    {getFieldDecorator('financingNum', {
                        initialValue: '0'
                    })(
                        <Select
                            placeholder=""
                            className="financingNum"
                        >
                            <Option value="0"></Option>
                            {this.state.filterData.financingNumList.map(item =>
                                <Option key={item.key} value={item.key.toString()}>{item.value}</Option>
                            )}
                        </Select>
                    )}
                </FormItem>
                <FormItem>
                    <Button type="primary" onClick={this.handleSubmit.bind(this,1)}></Button>
                </FormItem>
            </Form>
        )
    }
}

const WrappedFilterForm = Form.create()(FilterForm);

module.exports = WrappedFilterForm;


