import React,{Component} from 'react'
import { hashHistory, Link } from 'react-router'
import { Row, Col } from 'antd';

class FundList extends React.Component {
    constructor(props) {
        super(props);
    }

    categoryText(type) {
        switch(type) {
            case "1":
                return "";
            case "2":
                return "";
            case "3":
                return "";
            case "4":
                return"";
        }
    }

    render() {
        return (
            <Row>
                {this.props.fundList.map(item =>
                    <Row key={item.id} className="itemDetail">
                        <Row>
                            {item.title}
                            <span className={item.status==1 ? "status progress content" : "status content"}>{item.status == 1 ? "" : ""}</span>
                        </Row>
                        <Row>
                            <Col span={5}>
                                <Row>
                                    :<span className={item.status==1 ? 'redText content' : 'overText content'}>{this.categoryText(item.category)}</span>
                                </Row>
                                <Row>
                                    :<span className={item.status==1 ? 'conetent' : 'overText content'}>{item.region.join(',')}</span>
                                </Row>
                            </Col>
                            <Col span={7}>
                                <Row>
                                    :<span className={item.status==1 ? 'redText content' : 'overText content'}>{item.type.join(",")}</span>
                                </Row>
                                <Row>
                                    :<span className={item.status==1 ? 'redText content' : 'overText content'}>{item.investMin}~{item.investMax}</span>
                                </Row>
                            </Col>
                            <Col span={7}>
                                <Row>
                                    :<span className={item.status==1 ? 'conetent' : 'overText content'}>{item.industry.join(",")}</span>
                                </Row>
                                <Row>
                                    :<span className={item.status==1 ? 'conetent' : 'overText content'}>{item.seen}</span>
                                </Row>
                            </Col>
                            <Col span={5}>
                                <Link className={item.status ? 'goToDetail' : 'goToDetail over'} to={`/fund/detail/${item.id}?type=${item.delegate ? 'delegate' : 'normal'}`}></Link>
                            </Col>
                        </Row>
                    </Row>
                )}
            </Row>
        )
    }
}

module.exports = FundList;


