import React,{Component} from 'react'
import { hashHistory, Link } from 'react-router'
import { Row, Col, Breadcrumb, Pagination } from 'antd';
import FilterForm from './filterForm.jsx'
import ProjectList from './projectList.jsx'

class List extends React.Component {
    constructor(props) {
        super(props);
        this.filterData = {};
        this.state = {
            filterData: {},
            projectList: [],
            page: 1,
            total: 0
        };
        ['getFilterResult', 'changePage'].forEach(m=>this[m] = this[m].bind(this));
    }

    changePage(page) {
        this.formRef.handleSubmit(page);
    }

    getFilterResult(data) {
        let that = this;
        this.filterData = this.dealReqData(data);
        ajax({
            url: "/project/getInfoList",
            data: that.filterData,
            callback(data) {
                that.setState({
                    projectList: data.projectList,
                    total: data.total,
                    page: that.filterData.page
                });
            }
        })
    }

    dealReqData(data) {
        let params = {};
        params.keyWord = this.props.location.query.keyWord ? this.props.location.query.keyWord : "";
        params.category = data.category[0];
        let stockType = [];
        if(data.category.length == 1) {
            if(data.category[0] == "2") {
                stockType = ["1", "2"];
            } else {
                stockType = [];
            }
        } else {
            stockType = [data.category[1].split('-')[1]]
        }
        params.stockType = stockType;
        params.type = data.type;
        params.industry = data.industry;
        params.region = data.region;
        params.financingNum = data.financingNum;
        params.page = data.page;
        return params;
    }

    render() {
        return (
            <Row>
                <Breadcrumb>
                    {this.props.routes.map(item =>
                        <Breadcrumb.Item key={item.breadcrumbName}>{item.breadcrumbName}</Breadcrumb.Item>
                    )}
                    {this.props.location.query.keyWord ?
                        <Breadcrumb.Item>{this.props.location.query.keyWord}</Breadcrumb.Item> : null
                    }
                </Breadcrumb>
                <Row className="filterForm">
                    <FilterForm wrappedComponentRef={(inst) => this.formRef = inst} getFilterResult={this.getFilterResult} />
                </Row>
                <Row className="projectList">
                    <ProjectList projectList={this.state.projectList} />
                    <Row className="pagination" type="flex" justify="center" align="middle">
                        <Pagination
                            current={this.state.page}
                            total={this.state.total}
                            onChange={this.changePage}
                        />
                    </Row>
                </Row>
            </Row>
        )
    }
}

module.exports = List;


